.Setings {
    width: 100%;
    color: black;
    margin: 0 auto;
    margin-top: 120px;

    // background-color: aqua;
    .Setings-menu {
        width: 20%;
        height: 100vh;
        background-color: gray;
        position: fixed;
        top: 80px;
        left: 0;
        

        h2 {
            margin-top: 30px;
            color: black;
            text-align: center;
            font-size: 35px;
            border-bottom: 1px solid ;
        }

        ul {
            margin-top: 70px;


            li {
                position: relative;
                cursor: pointer;
                width: 100%;
                padding: 15px;
                margin-top: 13px;
                text-align: center;
                background-color: black;
                list-style-type: none;
                color: white;
                p{
                    background-color: gray;
                    border-radius: 50%;
                    width: auto;
                    height: auto;
                    padding: 2px;
                    position: absolute;
                    top: 5px;
                    left: 60%;
                    color: black;
                }
                &:hover {
                    transition: .9s;
                    transform: translateY(-10px)scaleX(1.1);
                    background-color: gainsboro;
                    color: black;
                }
            }
            .activ{
                transition: .9s;
                transform: translateY(0)scaleX(1.1);
                background-color: rgb(94, 93, 93);
                color: white;
                clip-path: polygon(0% 0%, 100% 0%, 95% 50%, 100% 100%, 0% 100%);
            }
        }
    }

    .Setings-sayt,
    .Setings-login,
    .Setings-payment,
    .PostSetings {
        width: 40%;
        min-height: 100vh;
        margin: 0 auto;
        // background-color: rgb(0, 255, 4);
        text-align: center;

        h3 {
            font-size: 30px;
            margin-bottom: 40px;
        }

        div {
            margin-top: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            input {
                border: 1px solid;
                width: 70%;
            }
        }
    }
}